.pure-menu {
    background-color: #323F4D;
}

.menu-left h1 {
    color: #95BCE6;
    display: inline-block;
    vertical-align: middle;
    padding: .5em 1em;
    white-space: nowrap;
}

.menu-left h3 {
    color: #C1DFFF;
    display: inline-block;
    vertical-align: middle;
}

.menu-right ul li a {
    color: #C1DFFF;
}

.menu-right {
    margin: auto;
    text-align: center;
    padding-top: 26px;
    position: center;
}

.menu-right ul li :hover {
    background-color: #647D99;
}

@media screen and (max-width: 700px) {
    .menu-left h3 {
        display: none !important;
    }
}



